/*=============================
	Typography css start
===============================*/
body {
    font-family: var(--tc-body-font-family);
    font-size: var(--tc-body-font-size);
    font-weight: var(--tc-fw-regular);
    color: var(--tc-body-text);
    line-height: var(--tc-body-line-height);
}
body {
    &.elearning {
        --tc-body-font-family: 'Rubik', sans-serif;
        --tc-heading-font-family: 'Space Grotesk', sans-serif;
        --tc-btn-font-family: 'Space Grotesk', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-theme-primary);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
    }
    &.law-firm {
        --tc-body-font-family: 'Roboto', sans-serif;
        --tc-heading-font-family: 'Roboto', sans-serif;
        --tc-btn-font-family: 'Roboto', sans-serif;
        strong {
            font-family: 'Playfair Display', serif;
            font-style: italic;
            font-weight: 400;
            letter-spacing: -0.48px;
        }
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-navy-blue);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .custom-container {
            max-width: 1600px;
            margin-right: auto;
            margin-left: auto;
        }
    }
    &.finance-consultant {
        --tc-body-font-family: 'Epilogue', sans-serif;
        --tc-heading-font-family: 'Epilogue', sans-serif;
        --tc-btn-font-family: 'Epilogue', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-green-5);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
    }
    &.marketing-agency {
        --tc-body-font-family: 'Sora', sans-serif;
        --tc-heading-font-family: 'Sora', sans-serif;
        --tc-btn-font-family: 'Sora', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-black);
            font-weight: var(--tc-fw-semi-bold);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 0;
        }
    }
    &.insurance-consultancy {
        --tc-body-font-family: 'Schibsted Grotesk', sans-serif;
        --tc-heading-font-family: 'Schibsted Grotesk', sans-serif;
        --tc-btn-font-family: 'Schibsted Grotesk', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-radish);
            font-weight: var(--tc-fw-bold);
            font-weight: var(--tc-fw-bold);
        }
        .btn {
            border-radius: 0;
        }
    }
    &.application {
        --tc-body-font-family: 'Manrope', sans-serif;
        --tc-heading-font-family: 'Manrope', sans-serif;
        --tc-btn-font-family: 'Manrope', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            font-weight: var(--tc-fw-bold);
        }
    }
    &.coworking-space {
        --tc-body-font-family: 'Sora', sans-serif;
        --tc-heading-font-family: 'Sora', sans-serif;
        --tc-btn-font-family: 'Sora', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-black-2);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 0;
        }
    }
    &.seo-agency,
    &.startup {
        blockquote {
            color: #181a2a;
            /* Article/quote/Regular */
            font-family: 'Source Serif Pro';
            font-size: 24px;
            font-style: italic;
            font-weight: 400;
            line-height: 32px; /* 133.333% */
        }
    }
    &.fintech-app {
        --tc-body-font-family: 'Plus Jakarta Sans', sans-serif;
        --tc-heading-font-family: 'Plus Jakarta Sans', sans-serif;
        --tc-btn-font-family: 'Plus Jakarta Sans', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-white);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .content-top {
            border-top: 1px dashed var(--tc-system-green-4);
        }
    }
    &.charity {
        --tc-body-font-family: 'Roboto', sans-serif;
        --tc-heading-font-family: 'DM Serif Display', sans-serif;
        --tc-btn-font-family: 'Roboto', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-black-2);
            font-weight: var(--tc-fw-regular);
            line-height: var(--tc-heading-line-height);
        }
        .content-top {
            border-top: 1px dashed var(--tc-system-green-4);
        }
        .btn-filter {
            font-family: 'DM Serif Display', sans-serif;
            font-size: 14px;
            font-weight: 400;
        }
    }
    &.logistics {
        --tc-body-font-family: 'Sora', sans-serif;
        --tc-heading-font-family: 'Sora', sans-serif;
        --tc-btn-font-family: 'Sora', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-navy-blue-2);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 0;
        }
        .custom-container {
            max-width: 1920px;
            margin-right: auto;
            margin-left: auto;
        }
    }
    &.support-desk {
        .custom-container {
            max-width: 1920px;
            margin-right: auto;
            margin-left: auto;
        }
    }
    &.event {
        --tc-body-font-family: 'Roboto', sans-serif;
        --tc-heading-font-family: 'Poppins', sans-serif;
        --tc-btn-font-family: 'Roboto', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-navy-blue-3);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .btn,
        .btn-change-text {
            border-radius: 0;
        }
    }
    &.fintech-solution {
        --tc-body-font-family: 'Sora', sans-serif;
        --tc-heading-font-family: 'Sora', sans-serif;
        --tc-btn-font-family: 'Sora', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-black);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .custom-container {
            max-width: 1500px;
            margin-right: auto;
            margin-left: auto;
        }
        .btn {
            border-radius: 50rem;
        }
    }
    &.digital-agency {
        --tc-body-font-family: 'DM Sans', sans-serif;
        --tc-heading-font-family: 'DM Sans', sans-serif;
        --tc-btn-font-family: 'DM Sans', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-navy-blue-3);
            font-weight: var(--tc-fw-bold);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 50rem;
        }
        .btn-text {
            font-weight: 700;
        }
        strong {
            font-family: 'DM Serif Display', serif;
            font-style: italic;
            font-weight: 400;
            .line1 {
                width: 270px;
                height: 10px;
                display: inline-block;
                background: var(--tc-theme-primary);
            }
            .line2 {
                width: 198px;
                height: 10px;
                display: inline-block;
                background: var(--tc-theme-primary);
            }
        }
    }
    &.creative-studio {
        --tc-body-font-family: 'Sora', sans-serif;
        --tc-heading-font-family: 'DM Serif Display', sans-serif;
        --tc-btn-font-family: 'Sora', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-navy-black);
            font-weight: var(--tc-fw-regular);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 8px;
        }
        strong {
            font-family: 'DM Serif Display', serif;
            font-style: italic;
            font-weight: 400;
        }
    }
    &.yoga {
        --tc-body-font-family: 'Roboto', sans-serif;
        --tc-heading-font-family: 'DM Serif Display', sans-serif;
        --tc-btn-font-family: 'Roboto', sans-serif;
        h1,
        h2,
