            font-weight: var(--tc-fw-regular);
            line-height: var(--tc-heading-line-height);
        }
        .btn {
            border-radius: 0px;
        }
    }
    &.crypto-agency {
        --tc-body-font-family: 'Roboto', sans-serif;
        --tc-heading-font-family: 'Roboto', sans-serif;
        --tc-btn-font-family: 'Roboto', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-white);
            font-weight: var(--tc-fw-semi-bold);
            line-height: var(--tc-heading-line-height);
        }
        .custom-container {
            max-width: 1600px;
            margin-right: auto;
            margin-left: auto;
        }
    }
    &.news-magazine {
        --tc-body-font-family: 'Inter', sans-serif;
        --tc-heading-font-family: 'Inter', sans-serif;
        --tc-btn-font-family: 'Inter', sans-serif;
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: var(--tc-system-black);
            font-weight: var(--tc-fw-semi-bold);
            line-height: var(--tc-heading-line-height);
        }
    }
}
::selection {
    background-color: var(--tc-theme-primary);
    color: var(--tc-system-white);
}
::-moz-selection {
    background-color: var(--tc-theme-primary);
    color: var(--tc-system-white);
}

a,
button {
    font-family: var(--tc-btn-font-family);
    color: var(--tc-system-black);
    outline: none;
    text-decoration: none;
    transition: 0.3s;
}

button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
    outline: 0 !important;
    box-shadow: none !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--tc-heading-font-family);
    color: var(--tc-system-black);
    margin-top: 0px;
    font-weight: var(--tc-fw-semi-bold);
    line-height: var(--tc-heading-line-height);
    text-transform: unset;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}
h1 {
    font-size: 64px;
}
h2 {
    font-size: 48px;
}
h3 {
    font-size: 40px;
}
h4 {
    font-size: 36px;
}
h5 {
    font-size: 32px;
}
h6 {
    font-size: 24px;
}

p {
    font-family: var(--tc-body-font-family);
    font-size: var(--tc-body-font-size);
    line-height: var(--tc-body-line-height);
    font-weight: var(--tc-fw-regular);
    color: var(--tc-body-text);
    margin-bottom: 15px;
}
hr {
    border-bottom: 1px solid var(--tc-color-gray-1);
    border-top: 0 none;
    margin: 30px 0;
    padding: 0;
}
label {
    color: var(--tc-heading-color);
    cursor: pointer;
    font-size: var(--tc-body-font-size);
    font-weight: var(--tc-fw-regular);
}
input[type='color'] {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background: none;
    border: 0;
    cursor: pointer;
    height: 100%;
    width: 100%;
    padding: 0;
    border-radius: 50%;
}
/*=============================
    - Input Placeholder
===============================*/
input,
textarea {
    color: var(--tc-body-text);
}
*::-moz-placeholder {
    color: var(--tc-body-text);
    font-size: var(--tc-fs-7);
    opacity: 1;
}
*::placeholder {
    color: var(--tc-body-text);
    font-size: var(--tc-fs-7);
    opacity: 1;
}
.list-wrap {
    margin: 0px;
    padding: 0px;
    & li {
        list-style: none;
    }
}
img,
.img {
    max-width: 100%;
    transition: 0.3s;
}
a:focus,
.btn:focus,
.button:focus {
    text-decoration: none;
    outline: none;
    box-shadow: none;
}
a:hover {
    color: var(--tc-theme-primary) !important;
    text-decoration: none;
}
.btn:hover {
    color: var(--bs-btn-hover-color) !important;
    background: var(--bs-btn-hover-bg);
    border-color: var(--bs-btn-hover-border-color);
}
