:root {
    --bs-border-color: var(--tc-system-stroke) !important;
}

// color-homepage
body {
    &.startup,
    &.data-analysis,
    &.tax-software,
    &.marketing-agency,
    &.fintech-solution {
        --tc-theme-primary: var(--tc-system-indigo);
        --bs-border-radius-xl: 10px !important;

        .border-primary {
            --bs-primary-rgb: 121, 74, 255;
        }

        .bg-secondary-2 {
            background: #f4f1ff;
        }

        .border-primary-2 {
            border: 1px solid #e0d6ff;
        }
    }

    &.data-analysis {
        --bs-primary-rgb: 121, 74, 255;

        .bg-secondary-2 {
            background-color: #edf1fc;
        }
    }

    &.ai-solutions {
        --tc-theme-primary: var(--tc-system-indigo);
        --bs-border-radius-xl: 10px !important;

        .border-primary {
            --bs-primary-rgb: 121, 74, 255;
        }

        .text-linear {
            background: var(--tc-linear-3);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        .bg-dark-1 {
            background-color: #020202 !important;
        }

        .bg-dark {
            background-color: #0a0614 !important;
        }

        .bg-secondary-2 {
            background-color: #f1f1f1;
        }

        .border-primary-2 {
            border: 1px solid #e0d6ff;
        }

        .btn-outline-dark {
            --bs-btn-hover-bg: var(--tc-linear-3);
            --bs-btn-hover-border-color: var(--tc-linear-3);
            --bs-btn-active-bg: var(--tc-linear-3);
            --bs-btn-active-border-color: var(--tc-linear-3);
        }
        .btn-white {
            &:hover {
                --bs-btn-hover-color: var(--tc-system-black);
            }
        }
    }

    &.it-solutions {
        --bs-border-radius-xl: 10px !important;
    }

    &.business {
        --bs-border-radius-xl: 10px;
        --bs-border-radius-xxl: 20px;

        .bg-secondary-3 {
            background: #eceff1;
        }

        .button--calypso::before {
            background: #094cdf;
        }

        .button--calypso::after {
            background: #094cdf;
        }
    }

    &.marketing-agency {
        .bg-secondary-2 {
            background-color: #f0f4fd;
        }
    }

    &.support-desk {
        --tc-theme-primary: var(--tc-system-green);
        --bs-primary-rgb: 26, 170, 89;
        --bs-secondary-rgb: 239, 244, 249;
        --bs-border-radius-xl: 10px;
        --bs-border-radius-xxl: 20px;

        .bg-secondary-2 {
            background: #eff4f9;
        }
    }

    &.accounting-service {
        --tc-theme-primary: var(--tc-system-radish);
        --bs-border-radius-xxl: 20px;
        --bs-primary-rgb: 0, 81, 83;

        .border-primary {
            border-color: var(--tc-system-radish) !important;
        }

        .bg-secondary-2 {
            background: #eceff1 !important;
        }

        .text-secondary-2 {
            color: #9abbbc;
        }
    }

    &.conference {
        --tc-theme-primary: var(--tc-system-red);
        --bs-border-radius-xxl: 20px;
        --bs-primary-rgb: 214, 10, 43;

        .bg-secondary-2 {
            background: #fff1f3 !important;
        }
    }

    &.elearning {
        --tc-theme-primary: var(--tc-system-green-2);
        --bs-border-radius-xl: 10px;
        --bs-border-radius-xxl: 20px;
        --bs-primary-rgb: 1, 71, 60;

        .bg-secondary-2 {
            background-color: #f3f3e5;
        }

        .btn-filter {
            --bs-btn-color: var(--tc-system-black-2);
            --bs-btn-bg: var(--tc-system-white);
            --bs-btn-border-color: var(--tc-system-stroke);
            --bs-btn-hover-color: var(--tc-system-white);
            --bs-btn-hover-bg: var(--tc-theme-primary);
            --bs-btn-hover-border-color: var(--tc-system-black-2);
            --bs-btn-focus-shadow-rgb: 108, 117, 125;
            --bs-btn-active-color: var(--tc-system-white);
            --bs-btn-active-bg: var(--tc-theme-primary);
            --bs-btn-active-border-color: var(--tc-theme-primary);
            --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
            padding: 15px 24px;
            text-transform: uppercase;
        }
    }

    &.law-firm {
        --tc-theme-primary: var(--tc-system-gold);

        .bg-secondary-2 {
            background-color: #f6f3ef;
        }

        .bg-dark {
            background-color: var(--tc-system-navy-blue) !important;
        }

        .border-primary {
            --bs-primary-rgb: 185, 142, 68 !important;
        }

        .btn {
            border-radius: 0;
        }
    }

    &.finance-consultant {
        --tc-theme-primary: var(--tc-system-indigo-light);
        --tc-system-black: var(--tc-system-navy-blue);
        --bs-primary-rgb: 205, 203, 255;

        .btn {
            border-radius: 0;
        }

        .btn-primary {
            --bs-btn-color: var(--tc-system-green-5);
            --bs-btn-hover-color: var(--tc-system-green-5);
        }

        .btn-text {
            font-weight: 500;
            text-transform: capitalize;
        }

        .text-dark {
            color: var(--tc-system-green-4);
        }

        .bg-primary-2 {
            background-color: #eaebfa;
        }
    }

    &.insurance-consultancy {
        --tc-theme-primary: var(--tc-system-orange);
        --bs-primary-rgb: 255, 101, 46;

        .bg-primary-2 {
            background-color: var(--tc-system-radish);
        }

        .text-primary-2 {
            color: var(--tc-system-radish);
        }

        .bg-secondary-2 {
            background: #f3f6f7 !important;
        }

        .bg-secondary-3 {
            background: #e6edee !important;
        }

        .border-primary-2 {
            --bs-border-color: var(--tc-system-radish);
        }

        .fill-primary-2 {
            path {
                fill: var(--tc-system-radish) !important;
            }

            fill: var(--tc-system-radish) !important;
        }
    }

    &.coworking-space {
        --tc-theme-primary: var(--tc-system-green);
        --bs-primary-rgb: 26, 170, 89;

        .bg-primary-2 {
            background-color: var(--tc-system-radish);
        }

        .text-primary-2 {
            color: var(--tc-system-radish);
        }

        .text-secondary-2 {
            color: #f1f3f8 !important;
        }

        .bg-secondary-2 {
            background: #f1f3f8 !important;
        }
    }

    &.fintech-app {
        --tc-theme-primary: var(--tc-system-green-4);
        --bs-primary-rgb: 177, 227, 70;
        --bs-border-radius-xl: 10px;

        .bg-dark-3 {
            background-color: #2d2d2d;
        }
    }

    &.charity {
        --tc-theme-primary: var(--tc-system-yellow);
        --bs-primary-rgb: 177, 227, 70;
        --bs-border-radius-xl: 10px;
        --bs-border-radius-xxl: 20px;

        .bg-dark-3 {
            background-color: #2d2d2d;
        }
    }

    &.logistics {
        --tc-theme-primary: var(--tc-system-orange);
        --bs-primary-rgb: 255, 101, 46;
        --bs-border-radius-xxl: 20px;

        .border-primary-2 {
            --bs-border-color: var(--tc-system-navy-blue-2);
        }

        .bg-dark-3 {
            background-color: #2d2d2d;
        }

        .bg-primary-2 {
            background-color: var(--tc-system-navy-blue-2);
        }

        .text-primary-2 {
            color: var(--tc-system-navy-blue-2);
        }
    }

    &.event {
        --tc-theme-primary: var(--tc-system-green);
        --bs-primary-rgb: 26, 170, 89;

        .btn-white {
            --bs-btn-hover-color: var(--tc-system-black);
        }

        .bg-dark {
            background-color: var(--tc-system-navy-blue-3);
        }

        .text-secondary-2 {
            color: var(--tc-black-bg-body-text);
        }
    }

    &.digital-agency {
        --tc-theme-primary: var(--tc-system-green-4);
        --bs-primary-rgb: 26, 170, 89;

        .bg-linear {
            background: linear-gradient(180deg, var(--tc-theme-primary) 0%, rgba(255, 255, 255, 0.6) 100%);
        }

        .bg-dark {
            background-color: var(--tc-system-navy-blue-3);
        }

        .text-secondary-2 {
            color: var(--tc-black-bg-body-text);
        }
    }

    &.creative-studio {
        --tc-theme-primary: var(--tc-system-orange);
        --bs-primary-rgb: 255, 101, 46;

        .bg-secondary-2 {
            background-color: #fcf8f3;
        }
    }

    &.yoga {
        --tc-theme-primary: var(--tc-system-pink);
        --bs-primary-rgb: 212, 44, 108;

        .border-header {
            --bs-border-color: #f94b8d;
        }

        .text-secondary-2 {
            color: #f8f2f3;
        }

        .bg-secondary-2 {
            background-color: #f8f2f3;
        }
    }

    &.crypto-agency {
        .mobile-header-wrapper-style {
            background-color: #040826;
        }

        .mobile-header-wrapper-style .mobile-header-wrapper-inner .mobile-header-content-area .mobile-menu-wrap nav .mobile-menu li a {
            color: #fff;
        }

        background-color: #040826;
        --tc-theme-primary: #50eefa;
        --bs-primary-rgb: 80, 238, 250;
        --bs-border-radius-xl: 10px !important;
        --bs-border-radius-xxl: 20px !important;

        .bg-dark-2 {
            background-color: #03061c;
        }

        .text-secondary-2 {
            color: var(--tc-black-bg-body-text);
        }

        .text-linear {
            background: var(--Liner, linear-gradient(180deg, #fff 0%, rgba(26, 28, 76, 0) 100%));
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }

    &.news-magazine {
        --tc-theme-primary: #f7775e;
        --bs-primary-rgb: 247, 119, 94;

        .bg-secondary-2 {
            background-color: #ebeff4;
        }

        .btn {
            border-radius: 0;
        }

        .text-secondary {
            color: var(--tc-body-text) !important;
        }
    }
}

