    background-color: var(--tc-theme-primary) !important;
}
.bg-1 {
    background-color: var(--tc-bg-1) !important;
}

.bg-light {
    background-color: var(--tc-bg-light) !important;
}

.bg-light-2 {
    background-color: var(--tc-bg-light-2) !important;
}

.bg-radish {
    background-color: var(--tc-system-radish);
}

.bg-orange {
    background-color: var(--tc-system-orange) !important;
}

.bg-blue {
    background-color: var(--tc-system-blue) !important;
}

.bg-yellow {
    background-color: var(--tc-system-yellow) !important;
}

.bg-secondary-2 {
    background-color: #f5f5f5;
