// text colors
.text-primary {
    color: var(--tc-theme-primary) !important;
}

.text-dark {
    color: var(--tc-system-black) !important;
}

.text-dark-2 {
    color: var(--tc-system-black-2) !important;
}

.text-stroke {
    color: var(--tc-system-stroke);
}

.text-yellow {
    color: var(--tc-system-yellow) !important;
}

.text-gold {
    color: var(--tc-system-gold) !important;
}

.text-green-3 {
    color: var(--tc-system-green-3);
}

.text-body-black-bg {
    color: var(--tc-black-bg-body-text);
}

.text-secondary-2 {
    color: #afafaf;
}

.body-text {
    font-family: var(--tc-body-font-family);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    /* 162.5% */
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.content-top {
    .line {
        width: 20px;
        height: 1px;
        opacity: 0.5;
        background-color: var(--tc-theme-primary);
    }
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.fill-white {
    path {
        fill: var(--tc-system-white) !important;
    }

    fill: var(--tc-system-white) !important;
}

.fill-dark {
    path {
        fill: var(--tc-system-black) !important;
    }

    fill: var(--tc-system-black) !important;
}

.fill-yellow {
    path {
        fill: var(--tc-system-yellow) !important;
    }

    fill: var(--tc-system-yellow) !important;
}

.fill-green-3 {
    path {
        fill: var(--tc-system-green-3) !important;
    }

    fill: var(--tc-system-green-3) !important;
}

.fill-green-4 {
    path {
        fill: var(--tc-system-green-4) !important;
    }

    fill: var(--tc-system-green-4) !important;
}

.fill-green-5 {
    path {
        fill: var(--tc-system-green-5) !important;
    }

    fill: var(--tc-system-green-5) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}

.stroke-green-3 {
    path {
        stroke: var(--tc-system-green-3) !important;
    }

    circle {
        stroke: var(--tc-system-green-3) !important;
    }
}

// variables end
.border-linear {
    background:
        linear-gradient(#060b2e, #060b2e) padding-box,
        linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(26, 28, 76, 0)) border-box;
    border: 2px solid transparent;
}

.border-linear-primary {
    background:
        linear-gradient(#03061c, #03061c) padding-box,
        linear-gradient(to bottom, rgba(177, 227, 70, 0.3), rgba(26, 28, 76, 0)) border-box;
    border: 1px solid transparent;
}

.border-linear-green-3 {
    border-image-slice: 1;
    border-image-source: -webkit-linear-gradient(180deg, #d5d52b 0%, #01473c 100%) !important;
}

.text-gradient {
    background: -webkit-linear-gradient(left, #6d4df2, #8c71ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.border-yellow {
    --bs-border-color: var(--tc-system-yellow) !important;
}

.backdrop-filter-20 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
}

.backdrop-filter-50 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(50px);
}

.mask-image {
    mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0));
}

.mask-image-2 {
    mask-image: linear-gradient(-270deg, #fff 14.77%, rgba(255, 255, 255, 0) 100%);
}

.bg-dark {
    --bs-dark-rgb: 41, 41, 41;
}

.bg-dark-2 {
    background: var(--tc-system-black-2);
}

.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}

.bg-dark-5 {
    background-color: #24222c;
}
